// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'

function toggleOverflow(el: Element, toggle: boolean) {
  // The sidebar needs `overflow-auto` to scroll when the modal is not shown, but needs it removed to show the modal
  if (toggle) {
    el.classList.remove('overflow-auto')
  } else {
    el.classList.add('overflow-auto')
  }
}

observe('.js-favorites-details-component', {
  constructor: HTMLDetailsElement,
  initialize(el) {
    el.addEventListener('toggle', () => {
      const dashboardSidebar = document.querySelector('.dashboard-sidebar')!

      toggleOverflow(dashboardSidebar, el.open)
    })
  },
})
